import React from 'react'
import styled from 'styled-components'
import { Text, Progress } from '@pancakeswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import useGetLotteryHasDrawn from 'hooks/useGetLotteryHasDrawn'
import { useCurrentTime } from 'hooks/useTimer'
import {
  getLotteryDrawTime,
  getLotteryDrawStep,
  getTicketSaleTime,
  getTicketSaleStep,
} from '../helpers/CountdownHelpers'

const ProgressWrapper = styled.div`
  display: block;
  width: 100%;
`

const TopTextWrapper = styled.div`
  margin-top: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
`

const BottomTextWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
`

const StyledPrimaryText = styled(Text)`
  color: ${({ theme }) => (theme.isDark ? theme.colors.secondary : '#202224')};
  margin-right: 4px;
`
const LotteryProgress = () => {
  const TranslateString = useI18n()
  const lotteryHasDrawn = useGetLotteryHasDrawn()
  const currentMillis = useCurrentTime()
  const timeUntilTicketSale = getTicketSaleTime(currentMillis)
  const timeUntilLotteryDraw = getLotteryDrawTime(currentMillis)

  return (
    <ProgressWrapper>
      <Progress primaryStep={getLotteryDrawStep(currentMillis)} secondaryStep={getTicketSaleStep()} />
      <TopTextWrapper>
        <StyledPrimaryText fontSize="20px" bold>
         {/* {lotteryHasDrawn ? timeUntilTicketSale : timeUntilLotteryDraw} */}
         {TranslateString(0, '2 Meowth Lotteries per day!')}
         {/* <br/ > */}
         {/* {TranslateString(0, 'Winning tickets are drawn at:')}
         <br/ >
         {TranslateString(0, '0700 UTC and 1500 UTC')} */}
        </StyledPrimaryText>
        <Text fontSize="20px" bold color="contrast">
        {/*  {lotteryHasDrawn ? TranslateString(0, 'Until ticket sale') : TranslateString(0, 'Until lottery draw')} */}
        </Text>
      </TopTextWrapper>
      {/* {!lotteryHasDrawn && ( */}
        <BottomTextWrapper style={{marginTop: 15}}>
          <Text color="highlight" bold style={{fontSize: 20}}>
          {/* {TranslateString(0, 'Winning ticket is drawn 6 hours after sale starts')} */}
          {timeUntilLotteryDraw} {TranslateString(0, 'until lottery draw âœ¨')}
          </Text>
        </BottomTextWrapper>
      {/* )} */}
    </ProgressWrapper>
  )
}

export default LotteryProgress
